@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  /*Primary*/
  --Blue : hsl(246, 80%, 60%);
  --Light-orange : hsl(15, 100%, 70%);
  --Soft-blue : hsl(195, 74%, 62%);
  --Light-red : hsl(348, 100%, 68%);
  --Lime-green : hsl(145, 58%, 55%);
  --Violet : hsl(264, 64%, 52%);
  --Soft-orange : hsl(43, 84%, 65%);
  
  /*Neutral*/
  --Very-dark-blue : hsl(226, 43%, 10%);
  --Dark-blue : hsl(235, 46%, 20%);
  --Desaturated-blue : hsl(235, 45%, 61%);
  --Pale-Blue : hsl(236, 100%, 87%);
}
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body{
  font-size: 18px;
  font-family: 'Rubik', sans-serif;
}
.App{
  background-color: hsl(226, 43%, 10%);
  background-color: var(--Very-dark-blue);
  width: 100vw;
  height: 100vh;
}

.timeTracker{
  display: flex;
  padding: 100px;
  width: 100%;
  height: 635px;
}

.timeList{
  height: 140%;
  width : 20%;
  border-radius: 20px;
  position: relative;
  background-color: hsl(235, 46%, 20%);
  background-color: var(--Dark-blue);
}
.tracker{
  width:80%
}

@media (max-width: 420px) {
  .App{
    height: auto;
  }
  
  .timeTracker{
    flex-direction: column ;
    height: auto;
    padding: 100px 30px;
  }
  
  .timeList{
    height: 200px;
    width : 100%;
  }
  .tracker{
    width:100%
  }
}
.userInfo{
    position: absolute;
    height: 70%;
    width: 100%;
    border-radius: 20px;
    background-color: var(--Blue);
    padding: 25px;
    color: #fff;
}

.userInfo img{
    width: 70px;
    border: 2px solid #fff;
    border-radius: 50%;
}
.userInfo h4{
    margin-top: 40px;
    font-size: 0.9rem;
    font-weight: normal;
    color: var(--Pale-Blue)
}
.userInfo h1{
    font-weight: lighter;
}

@media (max-width: 420px) {
    .userInfo{
        display: flex;
        align-items: center;
    }
    .userInfo h4{
        margin-top: 0;
    }
    .userInfo h1{
        font-size: 24px;
    }
    .infoContainer{
        padding-left: 20px;
    }
}
.time{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 30%;
    padding: 25px;
}

.time li{
    list-style: none;
    color: var(--Pale-Blue);
    padding-bottom: 15px;
    text-transform: capitalize;
    font-weight: normal;
    font-size: 0.9rem;
    cursor: pointer;
}
.time li:hover, .time li.active{
    color: #fff;
}

@media (max-width: 420px) {
    .time{
        width: 100%;
    }
    .time li{
        font-size: 1.1rem;
    }
    .tabs{
        display: flex;
        width: 100%;
        justify-content: space-evenly;
    }
}
.trackerItem{
    margin-left: 25px;
    background-color: #fff;
    border-radius: 20px;
    position: relative;
    background-repeat: no-repeat;
    background-position: 90% -5%;
}
.trackerItem:nth-child(1),.trackerItem:nth-child(2),.trackerItem:nth-child(3){
    margin-bottom: 25px;
}
.trackerInfo{
    position: absolute;
    bottom: 0;
    height: 80%;
    width: 100%;
    border-radius: 18px;
    background-color: var(--Dark-blue);
    color:#fff;
    cursor: pointer;
}
.trackerInfo:hover{
    background-color: var(--Desaturated-blue);
}
.trackerInfo .header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 35px;
}
.trackerInfo .body{
    display: flex;
    flex-direction: column;
    margin: 0 35px;
}
.trackerInfo .hours{
    font-size: 40px;
    font-weight: lighter;
    margin-bottom: 10px;
}
.trackerInfo .prevLog{
    font-size: 14px;
    font-weight: lighter;
    text-transform: capitalize;
    color: var(--Pale-Blue)
}
.trackerItem.Work{
    background-image: url(/time-tracking/static/media/icon-work.a3f124e5.svg);
    background-color: var(--Light-orange);
}
.trackerItem.Play{
    background-image: url(/time-tracking/static/media/icon-play.e8d4e569.svg);
    background-color: var(--Soft-blue);
}
.trackerItem.Study{
    background-image: url(/time-tracking/static/media/icon-study.1f0a65e4.svg);
    background-color: var(--Light-red);
}
.trackerItem.Exercise{
    background-image: url(/time-tracking/static/media/icon-exercise.3e7b0282.svg);
    background-color: var(--Lime-green);
}
.trackerItem.Social{
    background-image: url(/time-tracking/static/media/icon-social.64be0c18.svg);
    background-color: var(--Desaturated-blue);
}
.trackerItem.Self.Care{
    background-image: url(/time-tracking/static/media/icon-self-care.a161aff5.svg);
    background-color: var(--Soft-orange);
}

@media (max-width: 420px) {
    .trackerItem{
        height: 160px;
        margin: 25px 0 0 0;
    }
    .trackerItem:nth-child(1), .trackerItem:nth-child(2), .trackerItem:nth-child(3){
        margin-bottom: 0;
    }
    .trackerInfo{
        height: 75%;
    }
    .trackerInfo .header{
        margin: 20px 35px 10px 35px;
    }
    .trackerInfo .body{
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
}
.tabContent{
    display:none;
}
.tabContent.active{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 140%;
    justify-content: space-between;
}
@media (max-width: 420px) {
    .tabContent.active{
        display: flex;
        flex-direction: column;
    }
}
